<template>
  <div class="dongtai">
      <div  ref="dongtai" style="width:600px;height:400px;">
       
      </div>
     
      <router-view></router-view>
  </div>
  
</template>

<script>
var colors = ['#5793f3', '#d14a61', '#675bba'];

var echarts = require('echarts')
const option={
       color: colors,
     tooltip: {
        trigger: 'none',
        // axisPointer: {
        //     type: 'cross'
        // }
    },
    legend: {
        data:['治愈人数', '死亡人数']
    },
    grid: {
        top: 70,
        bottom: 50
    },
    xAxis: [
        {
            type: 'category',
            axisTick: {
                alignWithLabel: true
            },
            axisLine: {
                onZero: false,
                lineStyle: {
                    color: colors[1]
                }
            },
            axisPointer: {
                label: {
                    formatter: function (params) {
                        return '降水量  ' + params.value
                            + (params.seriesData.length ? '：' + params.seriesData[0].data : '');
                    }
                }
            },
            data: ['11月', '12月', '1月', '2月', '3月', '4月', '5月', '6月', ]
        },
        {
            type: 'category',
            axisTick: {
                alignWithLabel: true
            },
            axisLine: {
                onZero: false,
                lineStyle: {
                    color: colors[0]
                }
            },
            axisPointer: {
                label: {
                    formatter: function (params) {
                        return '降水量  ' + params.value
                            + (params.seriesData.length ? '：' + params.seriesData[0].data : '');
                    }
                }
            },
           
        }
    ],
    yAxis: [
        {
            type: 'value'
        }
    ],
    series: [
        {
            name: '治愈人数',
            type: 'line',
            xAxisIndex: 1,
            smooth: true,
            data: [0, 5, 10, 60, 2000, 50000, 79000, 79000]
        },
        {
            name: '死亡人数',
            type: 'line',
            smooth: true,
            data: [0, 1, 100, 2000, 4200, 4300, 4643, 0]
        }
    ]
}
export default {
mounted(){
var mychart=echarts.init(this.$refs.dongtai)
mychart.setOption(option)
},
methods:{
  
}
}
</script>

<style scoped>
#back{
    position: absolute;
    top: 0px;
    right: 0px;
}
.dongtai{
     left: 350px;
  border: 1px dashed red;
  width: 600px;
  position: relative;
}
</style>